body {
  background-color: aquamarine;
}

button {
  width: 100px;
  height: 50px;
  border-radius: 20px;
  background-color: rgb(242, 204, 243);
  cursor: pointer;
  font-size: 15px;
  transition: 0.5s;
  border: 1px solid gray;
}
button:hover {
  transform: scale(1.1);
}

.butPan {
  display: flex;
  gap: 40px;
}

.inpPan {
  padding-top: 40px;
  display: flex;
  gap: 20px;
}

input {
  border-radius: 20px;
  border: 0px;
  height: 30px;
  text-align: center;
  cursor: pointer;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
}

h1 {
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(106, 106, 236);
  font-size: 100px;
}/*# sourceMappingURL=index.css.map */